 * {
background-color: blueviolet;
font-family: monospace;
color: rgb(23, 23, 74);
cursor: pointer;
}
.todoform{
  display: flex;
  align-items: centre;
  justify-content: center;

 
}
.todoinput{
  background-color: rgb(23, 23, 74);
color: aliceblue;
display: flex;
box-sizing: border-box;
border-color:rgb(65, 65, 189) ;
min-height: 70px;
min-width: 55vw;

font-size: x-large;
text-align: justify;
}
.todoinput:hover{
opacity: 0.95;
border-color: whitesmoke;
cursor: pointer;
height: 72px ;
width: 56vw;
transition-duration: 3ms;
}
.addtolist{
  align-content: space-between;
  background-color: blueviolet;
  color:rgb(23, 23, 74); ;
  height: 70px;
  width: 70px;
}
.addtolist:hover{
  opacity: 0.95;
  border-color: whitesmoke;
  cursor: pointer;
 transition-duration: 3ms;
  }
  .icons{
    display: flex;
    align-items: centre;
    justify-content: center;
  }